@import url(https://fonts.googleapis.com/css2?family=Inter:wght@200;400;700;900&display=swap);
:root {
  --bg-color: #f4f5f7;
  --bg-color--card: #fff;
  --border-radius--card: 1rem;
  --shadow--card: rgb(0 0 0 / 12%) 0px 2px 4px;
  --gradient: linear-gradient(135deg, #2ab7cf 10%, #30ce71 100%);
  --main-text-color: #2d314e;
  --gray-text-color: #9396a5;
  --fw-light: 200;
  --fw-regular: 400;
  --fw-bold: 700;
  --fw-size: 1.6rem;
}

html {
  font-size: 62.5%;
}

body {
  width: 100%;
  height: 100vh;
  font-family: 'Inter', sans-serif;
  background-color: #f4f5f7;
  background-color: var(--bg-color);
  color: #2d314e;
  color: var(--main-text-color);
  font-size: 1.6rem;
  font-size: var(--fw-size);
}

header {
  margin: 2.5rem;
}

@media (max-width: 576px) {
  header {
    margin: 1rem;
  }
}

footer {
  color: var(--gray-text-color);
  display: flex;
  padding-bottom: 3rem;
  justify-content: center;
  text-align: center;
}

@media (max-width: 576px) {
